<template>
  <div
    class="message-input ml-2"
    :disabled="!isActiveText">
    <span class="charcounter">{{ charcounting }}</span>
    <textarea
      ref="messageInput"
      placeholder="message..."
      :maxlength="maxCount"
      v-model="message"
      @keydown.enter="submitMessage"
      :disabled="!isActiveText" />
    <button
      class="send"
      :class="{ hidden: !isActive }">
      <svg
        @click="submitMessage"
        viewBox="0 0 100% 100%">
        <path
          d="M36.511,17.594L2.371,2.932c-0.374-0.161-0.81-0.079-1.1,0.21C0.982,3.43,0.896,
          3.865,1.055,4.241l5.613,13.263L2.082,32.295c-0.115,0.372-0.004,
          0.777,0.285,1.038c0.188,0.169,0.427,0.258,0.67,0.258c0.132,0,0.266-0.026,0.392-0.08
          l33.079-14.078c0.368-0.157,0.607-0.519,0.608-0.919S36.879,17.752,36.511,17.594z M4.632,
          30.825L8.469,18.45h8.061c0.552,0,1-0.448,1-1s-0.448-1-1-1H8.395L3.866,5.751l29.706,
          12.757L4.632,30.825z"/>
      </svg>
    </button>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { EventBus } from '@/event-bus';
import transactionTransfer from '@/modules/transfertransaction';

const transfer = transactionTransfer.create();
export default {
  name: 'MessageInput',
  data() {
    return {
      amount: 0,
      message: '',
      isActive: false,
      isActiveText: true,
      maxchar: 0,
    };
  },
  mounted() {
    const thisComponent = this;
    EventBus.$on('focus-message-input', () => {
      if (thisComponent.$refs.messageInput) {
        thisComponent.$refs.messageInput.value = '';
        thisComponent.message = '';
        thisComponent.$refs.messageInput.focus();
      }
    });
  },
  computed: {
    ...mapGetters({
      maxCount: 'getMaxChar',
      chatEncrypt: 'getEncrypt',
    }),
    charcounting() {
      return this.maxCount - this.message.length;
    },

  },
  watch: {
    message() {
      this.isActive = this.message.length ? true : false;
    },
  },
  methods: {
    ...mapActions([
      'setMessage',
      'setMe',
    ]),
    visibility() {
      this.isActive = this.isActive !== true;
      this.isActiveText = this.isActiveText !== true;
    },
    async sending() {
      const state = this.$store.state;
      const type = state.base.type;
      if (!this.message.length || !state.base.current) throw new Error('empty message!');
      const recipient = state[type].list[state.base.current];
      const message = {
        amount: this.amount,
        recipientAddress: recipient.address,
        recipientPublicKey: recipient.publicKey,
        message: this.message,
        type: this.chatEncrypt ? 2 : 1,
        isMultisig: true,
      };
      const result = await transfer.Send('transferTransaction', message);
      this.message = '';
      if (type === 'chats') {
        this.$store.dispatch(`${type}/setMessage`, {
          hash: result.transactionHash.data.slice(0, 8),
          recipient: {
            address: recipient.address,
            publicKey: recipient.publicKey,
          },
          payload: {
            timestamp: new Date().toLocaleString(),
            text: message.message,
            sender: state.base.me,
          },
        });
      }
      return result;
    },
    async submitMessage(event) {
      if (!event.shiftKey) {
        event.preventDefault();
      } else {
        return;
      }
      this.isActive = false;
      this.isActiveText = false;
      try {
        await this.sending();
      } catch (err) {
        EventBus.$emit('alert', { text: err.message, type: 'danger' });
      }
      this.isActive = true;
      this.isActiveText = true;
    },
  },

};
</script>

<style scoped>
.message-input {
    padding: 2px;
    display: flex;
    margin-bottom: 0.5rem;
    border: 1px solid #6AACDE;
    }
.send {
  background: none;
  border: none;
  margin-top: 3px;
  outline: none;
  float: left;
  cursor: pointer;
}
textarea {
    width: 90%;
    padding: 0 5px;
    resize: none;
    outline: none;
    font-family: Helvetica;
    border-color: #6AACDE;
    border: 0;
}
.send svg {
    width: 39px;
    height: 35px;
    fill: #077dd6;
}
.hidden {
  display: none;
}
.charcounter {
  display: -webkit-box;
  -webkit-box-align: center;
  padding: .5rem .5rem;
  color: #077dd6;
}
*[disabled]{
background: rgb(140, 177, 206)
}
</style>



