<template>
  <li
    class="dialog-item"
    :class="selected"
    @click="onFocus">
    <div class="row mr-0 p-2">
      <div class="col-11 pr-0">
        <span class="name">{{ address }}</span>
        <span class="lastMessage">{{ lastMessage }}</span>
      </div>
      <div class="col-1 text-center pr-0">
        <span
          class="close"
          aria-label="Remove dialog"
          @click="delDialog">
          <span aria-hidden="true" class="float-right">&times;</span>
        </span>
      </div>
  </div></li>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import { EventBus } from '../event-bus';
import Sound from '../modules/sound';

export default {
  name: 'DialogItem',
  props: {
    type: {
      type: String,
      default: null,
    },
    address: {
      type: String,
      default: null,
    },
    encrypt: {
      type: Boolean,
      default: true,
    },
    maxchar: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      state: {
        type: this.type,
        address: this.address,
        encrypt: this.encrypt,
        maxchar: this.maxchar,
      },
    };
  },

  methods: {
    ...mapActions([
      'setCurrent',
      'setEncrypt',
      'setType',
      'setMaxChar',
      'setRightBar',
    ]),
    onFocus(event) {
      EventBus.$emit('focus-message-input', event);
      this.setRightBar('ChatContainer');
      this.setCurrent(this.dialogAddress);
      this.setEncrypt(this.dialogEncrypt);
      this.setType(this.dialogType);
      this.setMaxChar(this.dialogMaxChar);
      EventBus.$emit('mobileToggleBar');
    },
    delDialog() {
      EventBus.$emit('DelDialog', { type: this.type, address: this.address });
    },
  },
  computed: {
    ...mapGetters({
      isActiveSound: 'getSound',
    }),
    dialogType() {
      return this.state.type;
    },
    dialogAddress() {
      return this.state.address;
    },
    dialogEncrypt() {
      return this.state.encrypt;
    },
    dialogMaxChar() {
      return this.state.maxchar;
    },
    lastMessage() {
      return this.$store.getters[`${this.type}/getLastMessage`](this.dialogAddress);
    },
    selected() {
      return this.$store.state.base.current === this.dialogAddress
              && this.$store.state.base.type === this.dialogType ? 'selected' : '';
    },
  },
  watch: {
    lastMessage() {
      if (this.isActiveSound) Sound('static/sound/ding.ogg');
    },
  },
  beforeDestroy() {
    this.state.encrypt = true;
    this.state.address = null;
    this.state.type = null;
  },
};
</script>

<style scoped>
.dialog-item {
  width: 100%;
  /* height: 70px;
  padding: 5%; */
  box-sizing: border-box;
  border-bottom: solid 1px #6aacde;
  cursor: pointer;
}

.selected {
  background: #e0f2ff;
}

.dialog-item img {
  border-radius: 50%;
  height: 100%;
  margin-right: 5%;
  float: left;
}

.dialog-item span {
  display: block;
  text-align: left;
}

.text {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  float: left;
  right: 0.5rem;
}

span.name {
  font-weight: bold;
  font-size: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #000;
}

span.lastMessage {
  color: rgb(158, 156, 156);
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.close {
    float: none!important;
    color: #6aacde!important;
    line-height: 1.3;
}
.close:hover {
  color: #2779b8!important;
}
</style>



