<template>
  <div class="row-fluid">
    <div class="col-md-6 offset-md-3 pt-2">
      <h4 class="modal-title">Node list</h4>
      <div class="form-group">
        <textarea
          v-model="text"
          class="form-control"
          rows="15" />
        <button
          @click = "pushNodes"
          :disabled="disabled"
          type="button"
          class="mt-2 btn btn-primary btn-custom">Update</button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import nem from 'nem-sdk';

export default {
  name: 'Nodes',
  data() {
    return {
      text: '',
      disabled: false,
    };
  },
  methods: {
    close() {
      this.errors = [];
      this.$emit('close');
    },
    getNodes() {
      if (this.network) {
        this.text = '';
        for (const l of nem.model.nodes[this.network]) {
          this.text += `${l.uri}\n`;
        }
      }
    },
    pushNodes() {
      this.disable();
      let nodes = this.text.split('\n');
      const reg = new RegExp(
        `^(?:(?:https?):\/\/|www\.)(?:\([-A-Z0-9+&@#\/%=~_|$?!:,.]*\)|
            [-A-Z0-9+&@#\/%=~_|$?!:,.])*(?:\([-A-Z0-9+&@#\/%=~_|$?!:,.]*\)|
            [A-Z0-9+&@#\/%=~_|$])`, 'i',
      );
      nodes = nodes.filter((n) => {
        if (reg.test(n)) return true;
      });
      if (nodes.length) {
        nem.model.nodes[this.network] = [];
        for (const node of nodes) {
          nem.model.nodes[this.network].push({ uri: node });
        }
      }
      this.getNodes();
      this.disable();
    },
    disable() {
      this.disabled = !this.disabled;
    },
  },
  computed: {
    ...mapGetters({ network: 'getNetwork' }),
  },
  mounted() {
    this.getNodes();
  },
  watch: {
    network() {
      this.getNodes();
    },
  },
};
</script>
<style scoped>
.btn-custom {
  width: 100%;
  background-color: #6AACDE!important;
  border-color: #6AACDE;
}
.btn-custom:hover {
  border-color: #afdafb;
}
h4 {
  color: #6aacde;
}
</style>



