import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
const BASE = {
  namespaced: false,
  state: {
    me: '',
    isLoggedIn: false,
    network: 'mainnet',
    balance: 0,
    maxchar: 0,
    rightBar: 'ChatContainer',
    leftBar: 'DialogList',
    current: null,
    encrypt: true,
    type: null,
    sound: true,
  },
  mutations: {
    setMe(state, me) {
      state.me = me;
    },
    setCurrent(state, address) {
      state.current = address;
    },
    setEncrypt(state, type) {
      state.encrypt = type;
    },
    setType(state, type) {
      state.type = type;
    },
    setMaxChar(state, max) {
      state.maxchar = max;
    },
    setRightBar(state, stage) {
      state.rightBar = stage;
    },
    setLeftBar(state, stage) {
      state.leftBar = stage;
    },
    setLoggedIn(state, status) {
      state.isLoggedIn = status;
    },
    setNetwork(state, network) {
      state.network = network;
    },
    setBalance(state, balance) {
      state.balance = balance;
    },
    setSound(state) {
      state.sound = !state.sound;
    },
  },
  actions: {
    setMe({ commit }, me) {
      commit('setMe', me);
    },
    setCurrent({ commit }, address) {
      commit('setCurrent', address);
    },
    setEncrypt({ commit }, val) {
      commit('setEncrypt', val);
    },
    setType({ commit }, type) {
      commit('setType', type);
    },
    setMaxChar({ commit }, max) {
      commit('setMaxChar', max);
    },
    setRightBar({ commit }, stage) {
      commit('setRightBar', stage);
    },
    setLeftBar({ commit }, stage) {
      commit('setLeftBar', stage);
    },
    setLoggedIn({ commit }, status) {
      commit('setLoggedIn', status);
    },
    setNetwork({ commit }, network) {
      commit('setNetwork', network);
    },
    setBalance({ commit }, balance) {
      commit('setBalance', balance);
    },
    setDefaultState({ commit }) {
      commit('setDefaultState');
    },
    setSound({ commit }) {
      commit('setSound');
    },
  },
  getters: {
    getMe: state => state.me,
    getCurrent: state => state.current,
    getEncrypt: state => state.encrypt,
    getType: state => state.type,
    getMaxChar: state => state.maxchar,
    getNetwork: state => state.network,
    getBalance: state => state.balance,
    getRightBar: state => state.rightBar,
    getLeftBar: state => state.leftBar,
    getLoggedIn: state => state.isLoggedIn,
    getSound: state => state.sound,
  },
};

const CHATS = {
  namespaced: true,
  state: {
    list: {},
    messages: {},
  },
  mutations: {
    setChat(state, chatObj) {
      state.list[chatObj.address] = chatObj;
      // state.list = Object.assign({}, state.list)
      state.list = { ...state.list };
    },
    delChat(state, address) {
      delete state.list[address];
      state.list = { ...state.list };
    },
    setContainer(state, address) {
      state.messages[address] = {};
    },
    setMessage(state, data) {
      state.messages[data.recipient.address][data.hash] = data.payload;
      // state.messages = Object.assign({}, state.messages)
      state.messages = { ...state.messages };
    },
    delContainer(state, address) {
      delete state.messages[address];
      state.messages = { ...state.messages };
    },

  },
  actions: {
    setChat({ commit, state }, chatObj) {
      if (!Object.keys(state.list).includes(chatObj.address)) {
        commit('setChat', chatObj);
        commit('setContainer', chatObj.address);
      }
    },
    setMessage({ commit, state, dispatch }, data) {
      if (!state.list[data.recipient]) dispatch('setChat', data.recipient);
      if (!state.messages[data.recipient.address]) commit('setContainer', data.recipient.address);
      if (BASE.state.me === data.payload.sender) data.payload.me = true;
      if (!state.messages[data.recipient.address][data.hash]) commit('setMessage', data);
    },
    setContainer({ commit }, address) {
      commit('setContainer', address);
    },
    delChat({ commit }, address) {
      commit('delContainer', address);
      commit('delChat', address);
    },
  },
  getters: {
    getChats: state => state.list,
    getMessages: state => id => state.messages[id],
    getLastMessage: state => (id) => {
      if (!state.messages[id]) return;
      const keys = Object.keys(state.messages[id]);
      if (keys.length) return state.messages[id][keys.pop()].text;
    },
  },
};

const LIVES = {
  namespaced: true,
  state: {
    list: {},
    messages: {},
  },
  mutations: {
    setLive(state, liveObj) {
      state.list[liveObj.address] = liveObj;
      // state.list = Object.assign({}, state.list)
      state.list = { ...state.list };
    },
    delLive(state, address) {
      delete state.list[address];
      state.list = { ...state.list };
    },
    setContainer(state, address) {
      state.messages[address] = {};
    },
    delContainer(state, address) {
      delete state.messages[address];
      state.messages = { ...state.messages };
    },
    setMessage(state, data) {
      state.messages[data.recipient.address][data.hash] = data.payload;
      state.messages = { ...state.messages };
    },
  },
  actions: {
    setLive({ commit, state }, liveObj) {
      if (!Object.keys(state.list).includes(liveObj.address)) {
        commit('setLive', liveObj);
        commit('setContainer', liveObj.address);
      }
    },

    setMessage({ commit, state, dispatch }, data) {
      if (!state.list[data.recipient]) dispatch('setLive', data.recipient);
      if (!state.messages[data.recipient.address]) commit('setContainer', data.recipient.address);
      if (BASE.state.me === data.payload.sender) data.payload.me = true;
      if (!state.messages[data.recipient.address][data.hash]) commit('setMessage', data);
    },

    setContainer({ commit }, address) {
      commit('setContainer', address);
    },
    delLive({ commit }, address) {
      commit('delContainer', address);
      commit('delLive', address);
    },
  },
  getters: {
    getLives: state => state.list,
    getMessages: state => id => state.messages[id],
    getLastMessage: state => (id) => {
      if (!state.messages[id]) return;
      const keys = Object.keys(state.messages[id]);
      if (keys.length) return state.messages[id][keys.pop()].text;
    },
  },

};
const GROUPS = {
  namespaced: true,
  state: {
    list: {},
    messages: {},
  },
  mutations: {
    setGroup(state, chatObj) {
      state.list[chatObj.address] = chatObj;
      state.list = { ...state.list };
    },
    delGroup(state, address) {
      delete state.list[address];
      state.list = { ...state.list };
    },
    setContainer(state, address) {
      state.messages[address] = {};
    },
    delContainer(state, address) {
      delete state.messages[address];
      state.messages = { ...state.messages };
    },
    setMessage(state, data) {
      state.messages[data.recipient.address][data.hash] = data.payload;
      state.messages = { ...state.messages };
    },

  },
  actions: {
    setGroup({ commit, state }, chatObj) {
      if (!Object.keys(state.list).includes(chatObj.address)) {
        commit('setGroup', chatObj);
        commit('setContainer', chatObj.address);
      }
    },
    setMessage({ commit, state }, data) {
      if (!state.messages[data.recipient.address]) commit('setContainer', data.recipient.address);
      if (BASE.state.me === data.payload.sender) data.payload.me = true;
      if (!state.messages[data.recipient.address][data.hash]) commit('setMessage', data);
    },
    setContainer({ commit }, address) {
      commit('setContainer', address);
    },
    delGroup({ commit }, address) {
      commit('delContainer', address);
      commit('delGroup', address);
    },
  },
  getters: {
    getGroups: state => state.list,
    getMessages: state => id => state.messages[id],
    getLastMessage: state => (id) => {
      if (!state.messages[id]) return false;
      const keys = Object.keys(state.messages[id]);
      if (keys.length) return state.messages[id][keys.pop()].text;
    },
  },
};
export default new Vuex.Store({
  modules: {
    base: BASE,
    chats: CHATS,
    lives: LIVES,
    groups: GROUPS,
  },
});



