import Vue from 'vue';
import BootstrapVue from 'bootstrap-vue';
import App from './App.vue';
import Account from './modules/account/account';
import { AddDialog, DelDialog } from './modules/dialog.model';
import { EventBus } from './event-bus';
import store from './store';
import {} from './modules/nodes.extend';

Vue.use(BootstrapVue);
function load(type, stat) {
  EventBus.$emit('conversation-loader', type, stat);
}
EventBus.$on('AddDialog', async (data) => {
  try {
    load(data.type, true);
    await AddDialog(data);
  } catch (err) {
    EventBus.$emit('alert', { text: `${err.message}`, type: 'danger' });
  } finally {
    load(data.type, false);
  }
});
EventBus.$on('DelDialog', async (data) => {
  try {
    DelDialog(data);
  } catch (err) {
    EventBus.$emit('alert', { text: `${err.message}`, type: 'danger' });
  }
});
store.watch(() => store.getters.getMe, async (value) => {
  try {
    await Account(value);
  } catch (err) {
    EventBus.$emit('alert', { text: `${err.message}`, type: 'danger' });
  }
}, { immediate: false });
Vue.config.productionTip = false;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  components: { App },
  template: '<App/>',
});



