export default class RecursionExecute {
  constructor (fn = '', time = 0) {
    this._time = time
    this._executable = fn
    this._execute = false
  }
  exec () {
    if (this._execute) {
      setTimeout(() => {
        try {
          this._executable()
          this.exec()
        } catch (e) {
          this._execute = false
        }
      }, this._time)
    }
  }
  set time (time) {
    this._time = time
  }
  set execute (exec) {
    this._execute = exec
  }
  set executable (fn) {
    this._executable = fn
  }
}



