import EventEmitter from 'events';
import nem from 'nem-sdk';

function getRandArr(arr) {
  return arr.sort(() => 0.5 - Math.random());
}
class API extends EventEmitter {
  constructor() {
    super();
    this._plusTimeDeadline = 3;
    this._network = {};
    this._nodes = (this._network.type === 'mainnet') ? nem.model.nodes.mainnet : nem.model.nodes.testnet;
    this._port = (this._network.type === 'mainnet') ? 7891 : nem.model.nodes.defaultPort;
    this._socketPort = (this._network.type === 'mainnet') ? 7779 : nem.model.nodes.websocketPort;
  }

  setNodes() {
    this._nodes = (this._network.type === 'mainnet') ? nem.model.nodes.mainnet : nem.model.nodes.testnet;
  }

  send(common, transactionEntity) {

    if (!this._nodes.length) throw new Error('not active nodes! check your network connection');
    const endpoint = nem.model.objects
      .create('endpoint')
      (this._nodes[Math.floor(Math.random() * this._nodes.length)].uri, this._port);
    return nem.model.transactions.send(common, transactionEntity, endpoint)
      .then((r) => {
        if (r.code >= 2) throw new Error(r.message);
        return Promise.resolve(r);
      })
      .catch((err) => {
        // if (err.code !== -1) throw new Error(err.data.error);
        this.remove(endpoint.host);
        transactionEntity.deadline += this._plusTimeDeadline;
        return this.send(common, transactionEntity);
      });
  }

  /**
   * @description
   * @param {string} address
   * @param {string} txHash - transaction's hash
   * @returns {Object}
  */
  transactionIncoming(address, txHash = '') {
    if (!nem.model.address.isValid(address)) throw new Error('the address isn\'t valid');
    if (!nem.model.address.isFromNetwork(address, this._network.id)) throw new Error('this address belongs to another network');
    if (!this._nodes.length) throw new Error('not active nodes! check your network connection');
    const endpoint = nem.model.objects.create('endpoint')(this._nodes[Math.floor(Math.random * this._nodes.length)].uri, this._port);
    return nem.com.requests.account
      .transactions.incoming(endpoint, address, txHash)
      .then(r => Promise.resolve(r))
      .catch((err) => {

        this.remove(endpoint.host);
        return this.transactionIncoming(address, txHash);
      });
  }

  /**
   * @description
   * @param {string} address
   * @throws {Object} error
   * @returns {Object}
   * @see NEM-SDK
  */
  getAddressData(address) {
    if (!nem.model.address.isValid(address)) throw new Error('the address isn\'t valid');
    if (!nem.model.address.isFromNetwork(address, this._network.id)) throw new Error('this address belongs to another network');
    if (!this._nodes.length) throw new Error('not active nodes! check your network connection');
    const endpoint = nem.model.objects.create('endpoint')(this._nodes[Math.floor(Math.random() * this._nodes.length)].uri, this._port);
    return nem.com.requests.account.data(endpoint, address)
      .then(r => Promise.resolve(r))
      .catch((err) => {
        this.remove(endpoint.host);
        return this.getAddressData(address);
      });
  }

  /**
   * @description removing from list not working node
   * @param {array} list of nodes
   * @param {string} node's address
   * @returns {Object} filtered array
  */
  remove(element) {
    this._nodes = [...this._nodes.filter((e) => { if (e.uri !== element) return true; })];
  }

  connectorCreate(address) {
    if (!nem.model.address.isValid(address)) throw new Error('the address isn\'t valid');
    if (!nem.model.address.isFromNetwork(address, this._network.id)) throw new Error('this address belongs to another network');
    const _address = address;
    if (!this._nodes.length) throw new Error('not active nodes! check your network connection');
    const endpointWeb = nem.model.objects.create('endpoint')(this._nodes[Math.floor(Math.random() * this._nodes.length)].uri, this._socketPort);
    const connected = nem.com.websockets.connector.create(endpointWeb, _address);
    return connected.connect()
      .then(() => Promise.resolve(connected))
      .catch((err) => {
        this.remove(endpointWeb.host);
        return this.connectorCreate(_address);
      });
  }

  async createConnections(node, address) {
    const endpointWeb = nem.model.objects.create('endpoint')(node.uri, this._socketPort);
    const connected = nem.com.websockets.connector.create(endpointWeb, address);
    try {
      await connected.connect();
      return connected;
    } catch (e) {
      console.error(e);
      return null;
    }
  }

  async connectorCreateMulti(address, count = 1) {
    if (!nem.model.address.isValid(address)) throw new Error(`the address isn't valid`);
    if (!nem.model.address.isFromNetwork(address, this._network.id)) throw new Error('this address belongs to another network');
    // const _address = address;
    const connections = [];
    let nodes = [...getRandArr(this._nodes)];
    while ((connections.length < count) && (nodes.length)) {
      const connect = await this.createConnections(nodes[0], address);
      nodes = nodes.splice(1);
      if (connect) connections.push(connect);
    }
    if (!connections.length) throw new Error('not active nodes! check your network connection');
    return connections;
  }

  connector(connector) {
    return connector.connect();
  }

  subscribeAccountData(connector) {
    nem.com.websockets.requests.account.data(connector);
    nem.com.websockets.subscribe.account.data(connector, (r) => {
      this.emit(`account-data-${connector.listener}`, r);
    });
  }

  subscribeTransactionConfirmed(connector) {
    const socket = nem.com.websockets;
    nem.com.websockets.requests.account.data(connector);
    socket.subscribe.account.transactions
      .confirmed(connector, (r) => {
        this.emit(`transaction-confirmed-${connector.listener}`, r);
      });
  }

  subscribeTransactionUnconfirmed(connector) {
    nem.com.websockets.requests.account.data(connector);
    nem.com.websockets.subscribe.account.transactions
      .unconfirmed(connector, (r) => {
        this.emit(`transaction-unconfirmed-${connector.listener}`, r);
      });
  }

  set network(net) {
    this._network.type = net;
    this._port = (this._network.type === 'mainnet') ? 7891 : nem.model.nodes.defaultPort;
    this._socketPort = (this._network.type === 'mainnet') ? 7779 : nem.model.nodes.websocketPort;
    this._network.id = net === 'mainnet' ? 104 : -104;
    this.setNodes();
  }
}
export { API as default };



