<template>
  <div class="alert m-5 mobile-font">
    <h3>You need to top-up the balance at least 10 xem to complete registration</h3>
    <ul class="list-unstyled">
      <li>Registration spends only 0.5 XEM</li>
      <li>The more balance the more message could be send at the same time</li>
    </ul>
    <hr>
    <div class="row">
      <div class="col-lg-10 col-sm-12">
        <ul class="list-unstyled">
          <li><h5 class="alert-heading mobile-font">Balance {{ balance }} XEM {{ statusbalance }}</h5></li>
          <li><h5 class="alert-heading mobile-font">The registration is {{ statusregistration }}...</h5></li>
      </ul></div>
      <div class="col-lg-2 col-sm-12 text-center"><Qrcode :text="address"/></div>
    </div>
    <hr>
    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Address</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          v-model="address"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder="address"
          aria-label="address"
          readonly="readonly">
      </div>

    </div>
    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Publick key</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          :maxlength="64"
          v-model="publickey"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder="Publick key..."
          aria-label="Publick-key"
          readonly="readonly">
      </div>
    </div>
    <h4>***DON'T LOSE OR SHARE PRIVATE KEY***</h4>
    <h5>USE<a
      href="https://github.com/f0a18484/xsafe"
      target="_blank"> XSAFE</a>
      for storing private information in blockchain (powered by NEM)</h5>
    <div class="input-group mb-3">
      <div class="input-group-prepend p-0 col-lg col-sm-12">
        <span class="input-group-text w-100 mobile-font">Private key</span>
      </div>
      <div class="col-lg-10 p-0 w-100 col-sm-12">
        <input
          type="text"
          :maxlength="64"
          v-model="privatekey"
          class="form-control rounded-0 w-100 mobile-font"
          placeholder="Private key..."
          aria-label="Private-key">
      </div>
    </div>
    <Loader :height="220"/>
  </div>

</template>
<script>

import AccessController from '@/modules/access.controller';
import AccountController from '@/modules/account/account.controller';
import Loader from '@/components/Loader';
import { EventBus } from '@/event-bus';
import { mapActions, mapGetters } from 'vuex';
import transactionTransfer from '@/modules/transfertransaction';
import RecursionExecute from '@/modules/utils/recursion.execute';
import Qrcode from '@/components/Qr';

const user = AccessController.create();
const accountInf = new AccountController();
const transfer = transactionTransfer.create();
const recexec = new RecursionExecute();
export default {
  name: 'Signup',
  components: {
    Loader,
    Qrcode,
  },
  data() {
    return {
      address: '',
      publickey: '',
      privatekey: '',
      statusbalance: 'waiting...',
      statusregistration: 'waiting',
    };
  },
  methods: {
    ...mapActions([
      'setMe',
      'setLoggedIn',
      'setRightBar',
      'setLeftBar',
      'setNetwork',
      'setBalance',
    ]),
    ...mapGetters([
      'getNetwork',
      'getBalance',
    ]),
    async createAccount() {
      try {
        user.network = this.network;
        accountInf.network = this.network;
        await user.login(this.privatekey);
        this.address = user.data.master.address;
        this.publickey = user.data.master.publicKey;
        this.privatekey = user.data.master.privateKey;
        accountInf.address = this.address;
        const created = await this.uservalidate();
        if (!created) {
          recexec.execute = true;
          recexec.time = 10000;
          recexec.executable = this.getbalance;
          recexec.exec();
        }
      } catch (e) {
        EventBus.$emit('alert', { text: `${e.message}`, type: 'danger' });
      }
    },
    async getbalance() {
      const balance = await accountInf.getBalance(this.address);
      this.setBalance(balance);
    },
    async uservalidate() {
      const valid = await user.validateAccount(this.address);
      if (!valid) return false;
      this.setMe(user.data.master.address);
      this.setLoggedIn(true);
      this.setRightBar('ChatContainer');
      this.setLeftBar('DialogList');
      return true;
    },
    statusBalance() {
      this.statusbalance = 'complete!';
    },
    statusRegistration() {
      this.statusregistration = 'in process';
    },
  },
  computed: {
    network: {
      set(val) {
        this.setNetwork(val);
      },
      get() {
        return this.getNetwork();
      },
    },
    balance() {
      return this.getBalance();
    },
  },
  mounted() {
    this.privatekey = user.createPrivateKey();
  },
  watch: {
    network() {
      this.createAccount();
    },
    privatekey() {
      this.createAccount();
    },
    async balance() {
      if (this.balance >= 10) {
        try {
          this.statusBalance();
          await transfer.Send('aggregateTransaction');
          this.statusRegistration();
          recexec.executable = this.uservalidate;
        } catch (e) {
          EventBus.$emit('alert', { text: `${e.message}`, type: 'danger' });
        }
      }
    },
  },
  beforeDestroy() {
    recexec.execute = false;
  },

};
</script>
<style scoped>
.alert {
    background: #6aacde;
    color: #fff;
}
li {
  text-align: inherit;
}
@media (max-width: 575.98px) {
  h3 {
    font-size: 0.6rem!important;
  }
  h4 {
    font-size: 0.5rem!important;
  }
  h5 {
    font-size: 0.4rem!important;
  }
  .loader {
    height: 120px!important;
  }
  .mobile-font {
    font-size: 0.6rem!important;
  }
}
</style>



