<template>
  <div class="container-fluid">
    <Container/>
    <Alert
      :alerts=errors
    />
  </div>
</template>

<script>
import Container from '@/components/Container';
import Alert from '@/components/Alert';
import { EventBus } from './event-bus';

export default {
  name: 'App',
  components: {
    Container,
    Alert,
  },
  data() {
    return {
      errors: [],
    };
  },
  mounted() {
    EventBus.$on('alert', (msg) => {
      this.errors.push(msg);
    });
  },
  watch: {
    errors() {
      setTimeout(() => {
        if (this.errors.length) this.errors = this.errors.splice(1);
      }, 8000);
    },
  },
};
</script>
<style>
html {
  /* position:fixed; */
  width: 100%;
}
</style>



