<template>
  <div
    class="message-body"
    :class="{me: me}">
    <div
      class="message-text">
      <span class="sender">{{ sender }}</span>
      <span class="timestamp">{{ timestamp }}</span>
      <span class="text">{{ text }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'message-body',
  props: [
    'timestamp', 
    'text', 
    'me',
    'sender',
    ],
  data() {
    return {};
  },
  mounted() {
  },
};
</script>

<style scoped>
.message-body {
  display: block;
  max-width: 50%;
  margin-bottom: 4px;
  float: left;
  clear: both;
}

.message-text {
  padding: 8px;
  margin: 4px;
  text-align: left;
  border-radius: 4px;
  color: white;
  background-color: #4e8cff;
  word-wrap: break-word;
}

.message-body.me {
  float: right;
}

.message-body.me .message-text {
  background-color: #09980e;
}

.message-body span {
  display: block;
}
.sender {
  font-size: 0.8rem;
  font-style: italic;
}
.timestamp {
  font-size: 0.5rem;
  font-style: italic;
}
</style>



