
import Access from './access.controller';
import Account from './account/account.controller';
import { PrepareMessage, PreparePrivateMessage } from './utils/message';
import store from '../store';

const account = new Account();
const group = new Access();
let instance;
export default class DialogController {
  constructor() {
    if (instance) return instance;
    this.instance = this;
    this.store = {
      chats: {},
      lives: {},
      groups: {},
    };
  }

  setConnectors(val) {
    if (!val.type) throw new Error('nonexistent type');
    if (!val.address) throw new Error('nonexistent name');
    if (!val.connectors) throw new Error('nonexistent connectors');
    this.store[val.type][val.address] = val.connectors;
  }

  getConnectors(val) {
    if (!val.type || !this.store[val.type]) throw new Error('nonexistent type');
    if (!val.address || !this.store[val.type][val.address]) throw new Error('nonexistent name');
    return this.store[val.type][val.address];
  }

  async addChat(data) {
    const address = account.cleanAddress(data.address);
    if (store.state.chats.list[address]) throw new Error(`${address} is already in the Chat.`);
    account.network = store.state.base.network;
    const publickey = await account.getPublickey(address);
    if (!publickey) throw new Error('Need publick key');
    store.dispatch('chats/setChat', { address: address, publicKey: publickey });
  }

  closeConnections(data) {
    if (!data.type || !data.address) throw new Error('nonexistent arguments');
    const connectors = this.getConnectors(data);
    if (!connectors) throw new Error('nonexistent connectors');
    connectors.map(connector => connector.close());
    delete this.store[data.type][data.address];
  }

  async addLive(data) {
    const address = account.cleanAddress(data.address);
    if (store.state.lives.list[address]) throw new Error(`${address} is already in the Live.`);
    account.network = await store.state.base.network;
    const connectors = await account.TransactionUnconfirmed(address, 'Live');
    this.setConnectors({ address, type: 'lives', connectors });
    account.api.on(`transaction-unconfirmed-${address}-Live`, (trans) => {
      if (
        trans.transaction.type === 4100
          && trans.transaction.otherTrans.type === 257
          && trans.transaction.otherTrans.message.type === 1
          && trans.transaction.otherTrans.recipient === address
      ) {
        PrepareMessage(trans)
          .then((res) => {
            store.dispatch('lives/setMessage', res);
          });
      }
    });
    store.dispatch('lives/setLive', { address });
  }

  async addGroup(data) {
    group.network = await store.state.base.network;
    const newGroup = await group.createAccount(data.address);
    const address = account.cleanAddress(newGroup.address);
    if (store.state.groups.list[address]) throw new Error(`${address} is already in the Group.`);
    account.network = await store.state.base.network;
    const connectors = await account.TransactionUnconfirmed(address, 'Group');
    await this.setConnectors({ address, type: 'groups', connectors });
    account.api.on(`transaction-unconfirmed-${address}-Group`, (trans) => {
      if (
        trans.transaction.type === 4100
          && trans.transaction.otherTrans.type === 257
          && trans.transaction.otherTrans.message.type === 2
          && trans.transaction.otherTrans.recipient === address
      ) {
        PreparePrivateMessage(trans, newGroup.privateKey)
          .then((res) => {
            store.dispatch('groups/setMessage', res);
          });
      }
    });
    store.dispatch('groups/setGroup', newGroup);
  }
}



